import {makeAutoObservable} from 'mobx'
import uuid from "react-uuid";

class TaskStore {
    list = [
        {
            id: 1,
            name: '学习react',
            isDone: true
        },
        {
            id: 2,
            name: '搞定mobx',
            isDone: false
        }
    ]

    constructor() {
        makeAutoObservable(this)
    }

    get getDoneTaskCount() {
      return this.list.filter(task=>task.isDone===true).length
    }
    /**全选反选*/
    toggleAll = (e)=>{
        this.list = this.list.map(task=>{
            task.isDone = e.target.checked
            return task
        })
    }
    /**是否全选*/
    get isAll(){
        return this.list.every(task => task.isDone);
    }
    toggleTaskStatus = (taskId) => {
        this.list = this.list.map(task => {
            if (task.id === taskId) {
                task.isDone = !task.isDone
            }
            return task
        })
    }
    removeTask = (taskId) => {
        this.list = this.list.filter(task => task.id !== taskId)
    }
    addTask = (taskName) => {
        this.list.unshift({
            id: uuid(),
            name: taskName,
            isDone: false
        })
    }
}
export default TaskStore
