import './index.css'
import {useStore} from "../store";
import {observer} from "mobx-react-lite";
import {useEffect, useState} from "react";
import axios from "axios";
import {notification, Popconfirm, message} from "antd";

function Task() {
    const BASE_URL = "http://47.120.10.120:9090"
    const {taskStore} = useStore()
    const [taskName, setTaskName] = useState("")
    const [taskList, setTaskList] = useState([])
    const [finishTaskCount, setFinishTaskCount] = useState(0)
    const [taskTotal, setTaskTotal] = useState(0)
    const [isAll, setIsAll] = useState(false)
    const prepareToDo = (e) => {
        if (e.key === "Enter") {
            taskStore.addTask(e.target.value)
            setTaskName("")
        }
    }
    const openNotification = (message, description) => {
        notification.open({
            message,
            description,
            onClick: () => {
                console.log('Notification Clicked!');
            },
        });
    };
    const loadTaskList = async () => {
        const res = await axios.get(BASE_URL + "/todo/list")
        console.log(res.data.data)
        if (res.data.data) {
            setTaskList(res.data.data?.todoList)
            setTaskTotal(res.data.data?.taskTotal)
            setFinishTaskCount(res.data.data?.finishTaskCount)
            setIsAll(res.data.data?.isAll)
        } else message.info("操作失败");

    }
    useEffect(async () => {
        await loadTaskList()
    }, [])

    const toggleTaskStatus = async (id, isDone) => {
        if (isDone === "1") isDone = "0"
        else isDone = "1"
        await axios.post(BASE_URL + "/todo/toggle", {
            id,
            isDone
        })
        await loadTaskList()
    }
    /**
     * 控制全选，非全选
     */
    const toggleAll = async (e) => {
        await axios.post(BASE_URL + "/todo/toggle", {
            isDone: e.target.checked === true ? "1" : "0"
        })
        await loadTaskList()
    }
    const addTodo = async (e) => {

        if (e.key === "Enter") {
            if (taskName.length === 0) {
                notification.error({
                    message:"任务不能为空！！！"
                })
                return
            }
            if (taskName.length >= 25) notification.error({
                message:"最多只能输入25个字符哟!!!"
            })
            else{
                const res = await axios.post(BASE_URL + "/todo/add", {
                    taskName: taskName
                })
                if (res.data.data) notification.success({
                    message:"添加任务成功",
                    description:"今天记得要把他完成哟!!!"
                })
                else notification.error({
                    message:"添加任务失败!!!",
                    description:"请稍后再试"
                })
                await loadTaskList()
                setTaskName("")
            }
        }
    }

    const removeTask = async (id) => {
        const res = await axios.get(BASE_URL + "/todo/remove", {
            params: {
                id
            }
        })
        console.log(res)
        if (res.data.data) notification.success({
            message:"删除成功"
        })
        else notification.error({
            message:"删除失败，请稍后重试！"
        })
        await loadTaskList()
    }

    return (
        <section className="todoapp">
            <header className="header">
                <h1>TODO</h1>
                <input
                    className="new-todo"
                    autoFocus
                    autoComplete="off"
                    placeholder="What needs to be done?"
                    value={taskName}
                    onChange={(e) => setTaskName(e.target.value)}
                    onKeyUp={addTodo}
                />
            </header>
            <section className="main">
                <input
                    id="toggle-all"
                    className="toggle-all"
                    type="checkbox"
                    onChange={toggleAll}
                    checked={isAll}
                />
                <label htmlFor="toggle-all"></label>
                <ul className="todo-list">
                    {taskList.map(task =>
                        <li
                            className={task.isDone !== "0" ? "todo completed" : "todo"}
                            key={task.id}
                        >
                            <div className="view">
                                <input onChange={() => toggleTaskStatus(task.id, task.isDone)} className="toggle"
                                       type="checkbox" checked={task.isDone !== "0"}/>
                                <label>{task.taskName}</label>
                                <Popconfirm
                                    placement="right"
                                    title={"您确定要删除吗"}
                                    description={""}
                                    onConfirm={() => removeTask(task.id)}
                                    okText="Yes"
                                    cancelText="No"
                                >
                                    <button className="destroy"></button>
                                </Popconfirm>
                            </div>
                        </li>)}
                </ul>
            </section>
            <footer className="footer">
                <span className="todo-count">
                    任务总数:{taskTotal} 已完成:{finishTaskCount}
                </span>
            </footer>
        </section>
        // 全局store版本
        // <section className="todoapp">
        //     <header className="header">
        //         <h1>TODO</h1>
        //         <input
        //             className="new-todo"
        //             autoFocus
        //             autoComplete="off"
        //             placeholder="What needs to be done?"
        //             value={taskName}
        //             onChange={(e)=>setTaskName(e.target.value)}
        //             onKeyUp={prepareToDo}
        //         />
        //     </header>
        //     <section className="main">
        //         <input
        //             id="toggle-all"
        //             className="toggle-all"
        //             type="checkbox"
        //             onChange={taskStore.toggleAll}
        //             checked={taskStore.isAll}
        //         />
        //         <label htmlFor="toggle-all"></label>
        //         <ul className="todo-list">
        //             {taskStore.list.map(task =>
        //                 <li
        //                     className={task.isDone ? "todo completed" : "todo"}
        //                     key={task.id}
        //                 >
        //                     <div className="view">
        //                         <input onChange={() => taskStore.toggleTaskStatus(task.id)} className="toggle"
        //                                type="checkbox" checked={task.isDone}/>
        //                         <label>{task.name}</label>
        //                         <button onClick={() => taskStore.removeTask(task.id)} className="destroy"></button>
        //                     </div>
        //                 </li>
        //             )}
        //         </ul>
        //     </section>
        //     <footer className="footer">
        //         <span className="todo-count">
        //             任务总数:{taskStore.list.length} 已完成:{taskStore.getDoneTaskCount}
        //         </span>
        //     </footer>
        // </section>

    )
}

export default observer(Task)